  width: 300px;
  height: 200px;
  position: absolute;
  left: 50%;
  margin-left: -150px;
  margin-top: 30vw;
  background-image: -webkit-linear-gradient(left,red,#ff4444 10%,#ff5555 20%,#ff6666 30%, #ff7777 40%, #ff8888 50%,#ff8866 60%,#ff8866 70%,#ff8866 80%,#ff8866 90%,red 100%);
  -webkit-text-fill-color: transparent;/*  */
  -webkit-background-clip: text;/*  */
  -webkit-background-size: 200% 100%;
  -webkit-animation: masked-animation 4s linear infinite;
  font-size: 7vw;
  font-family: 'STHupo';
}
@-webkit-keyframes masked-animation {
  0% {
    background-position: 0  0;
  }
  100% {
    background-position: -100%  0;
  }
}
